import * as React from "react";
import { Link } from 'react-router-dom';
import { Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import styles from './OffersList.scss';
import icon1 from "../../../assets/images/offer/pudla_klapowe.png";
import icon2 from "../../../assets/images/offer/wykrojnikowe.png";
import icon3 from "../../../assets/images/offer/opakowania_z_nadrukiem.png";
import icon4 from "../../../assets/images/offer/przekladki_wypelnienia.png";
import icon5 from "../../../assets/images/offer/kratownica.png";
import icon6 from "../../../assets/images/offer/pianka.png";


class OffersList extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      offersList : [{name: 'puda klapowe', img: icon1, text: "Typowe puda klapowe z tektury falistej 3 i 5-cio warstwowej o dowolnej gramaturze. Bardzo uniwersalne i funkcjonalne opakowania w dobrej cenie."},
{name: 'opakowania wykrojnikowe', img: icon2, text: 'Profesjonalne maszyny pozwalaj nam na produkcj opakowa wykrojnikowych wedug indywidualnych zapotrzebowa klienta. Tego typu rodzaj opakowania pozwala nam na znacznie wicej. Cechuje go funkcjonalno i estetyka wykonania. Przy uyciu tego typu technologii moemy stworzy konstrukcje bardziej zoone o nietypowych ksztatach i w dowolnym rozmiarze:<ul><li>kartony samoskadajce si</li><li>kartony zamykane osobnym zamkniciem</li> <li>rnego typu tacki i pokrywy </li><li>kartony fasonowe z dowolnymi wyciciami (np. na donie)</li><li>kartony specjalne, nietypowe</li></ul>'},
{name: 'opakowania z nadrukiem', img: icon3, text: "Stosujemy nadruki fleksograficzne czyli metod  pozwalajc na naniesienie nadruku w dowolnym miejscu na opakowaniu, w jednym bd w dwch kolorach. Uywamy farb w peni ekologicznych."},
{name: 'przekadki, wkady, wypenienia', img: icon4, text: "Celem ich jest, zwikszenie wytrzymaoci opakowania, stabilizacja i separacja zapakowanych produktw."},
{name: 'kratownice', img: icon5, text: "Produkujemy kratownice w rnych gramaturach i rozmiarach. Su one do oddzielenia materiaw, ktre podatne s na zniszczenia czy otarcia."},
{name: 'przekadki oklejone piank', img: icon6, text: "Specjalizujemy si w oklejaniu tektury piank poliuretanow, ktra zapewni idealn ochron dla Twoich produktw. Przekadki tego typu amortyzuj wstrzsy, drgania minimalizujc ryzyko uszkodze mechanicznych, chroni przed wilgoci. "}]
      
     }
      
   };

  componentDidMount = () => {
     
      
  };
  scrollTo(element) {
    scroller.scrollTo(element, {
      duration: 1200,
      delay: 600,
      smooth: true,
      offset: 0
    })
  }
  render() {
    return (
    
    <section id="offers_list">
    <div className='container'>
    
    <div className="offers_table"  data-aos='fade-up' id='ContainerElementID'>
      {this.state.offersList.map((i, index) => {
      let myClass ='';
      let effect1 = '';
      let effect2 = '';
      let effect3 = '';

      if (index % 2 === 0) {
         myClass = 'offer_row even';
         effect1 = 'fade-up-right';
         effect2 = 'fade-right';
         effect3 = 'fade-left';
       } else {
         myClass='offer_row odd';
         effect1 = 'fade-up-left';
         effect2 = 'fade-left';
         effect3 = 'fade-right';
         
       }
      if (index === 1) {
         myClass +=' offer_anim1';
       } 
      if (index === 2) {
         myClass +=' offer_anim2';
       } 
        return <div className={myClass} key={index} id={'offer'+index} data-aos={effect1}>
        <div className="offer_photo_container">
        <div className="offer_photo" style={{
  backgroundImage: 'url(' + i.img + ')'
}}></div>
<div className="overflow_container">
              <div className="button">
                <Link to="/kontakt" className="site-nav__link">zapytaj!</Link>
              </div>
            </div>
</div>
        <div className="offer_description">
          <h3 className="offer_name">{i.name}</h3>          
          <div className="offer_text" dangerouslySetInnerHTML={{ __html: i.text }}></div>
        </div>
        </div>
        
      })}
        
        </div> 
           </div> 
    </section>     
 
    );
    }
};

export default OffersList;