import React from 'react';
import style from './ContactPage.scss';
import ContactContainer from '../../layouts/ContactContainer/ContactContainer';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCubes } from '@fortawesome/free-solid-svg-icons';
import MapContainer from '../../layouts/MapContainer/MapContainer';
import Loader from '../../layouts/Loader/Loader';

class Contact extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      loading: true,
      
      }
      this.timeout = null;

   };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    if(!this.props.isFirstLoading()){
       this.setPageLoading();
    } else{
       this.setState({loading: false});
    }
    

  };

  setPageLoading = () =>{
      let self = this;
      self.timeout = setTimeout(function(){
      self.setState({loading: false});
      self.props.scrollHandler(self.state.loading);
      }, 500);
  }

  componentWillUnmount = () => {
      clearTimeout(this.timeout);
  };

   render() {
  return (
  <div className="page_container">
  { this.state.loading ? <Loader loading={this.state.loading}/> : 
  <div id='contact_page'>
  <div className="container" data-aos="fade-down" data-aos-anchor-placement="center-center">
      <h2 className='contact_us' >Skontaktuj si z nami!</h2>     
    </div>
    <div className="contact_background">
      <div className='container'>
      <ContactContainer></ContactContainer>
      </div>
    </div>
    <div className='container'>
<span className='stamp' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes} />
</span>
</div>
    <MapContainer></MapContainer>
  </div>}
  </div>
    
  );
  }
}
export default Contact;