import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCubes, faClock, faBoxOpen, faUsers } from '@fortawesome/free-solid-svg-icons';

import styles from './Advantages.scss';

const advantages = [{image: faBoxOpen, description: 'realizujemy kade nawet najmniejsze zamwienie'},
{image: faUsers, description: 'realizujemy zamwienia wedug indywidualnych potrzeb klienta'},
{image: faClock, description: 'krtki czas realizacji'}];

const Advantages = () => {
let effects = ['fade-right', 'fade-down', 'fade-left'];
    return (
    
    <section id="advantages">
    <div className='container'>
    
    <div className='advantages_table'>
      {advantages.map((i, index) => {
        return <figure className="advantage" key={index} data-aos={effects[index]} data-aos-delay='2000'>
            <span className="advantage_icon">
                <FontAwesomeIcon icon={i.image} />
            </span>
            <h4 className="advantage_description">
            {i.description}
            </h4>
        </figure>
      })}
        
        </div> 
           </div> 
    </section>     
 
    );
};

export default Advantages;