import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTruckMoving, faCalendarAlt, faHandshake, faThumbsUp, faQuestionCircle, faTasks, faTools } from '@fortawesome/free-solid-svg-icons';

import styles from './AboutDescription.scss';
import certificate from "../../../assets/images/certificate_pl.jpg";

const description = [{icon: faHandshake, text: 'Jestemy firm dziaajc na rynku od maja 1994 roku, co w brany produkcji opakowa daje nam due dowiadczenie. Dowodem tego s zadowoleni ze wsppracy klienci oraz zdobywanie nowych rynkw zbytu.'},
{icon: faTools, text: 'Inwestujemy w urzdzenia ulepszajce jako naszych wyrobw. '},
{icon: faThumbsUp, text: 'Cechami, za ktre ceni nas klienci jest min: realizacja kadego nawet najmniejszego zamwienia, terminowo i elastyczno dostaw w zalenoci od potrzeb klienta. '},
{icon: faQuestionCircle, text: 'Przykadamy du wag do profesjonalnej obsugi klienta, zapewniamy mu fachowe wsparcia na kadym etapie realizacji zamwienia.'},
{icon: faTasks, text: 'Pomagamy w wyborze optymalnych rozwiza. Jestemy gotowi na podjcie rnorodnych projektw, nawet tych najbardziej wymagajcych.'},
{icon: faTruckMoving, text: 'Zapewniamy wasny elastyczny transport.'}];

const AboutDescription = () => {
    return (
    
    <section id="about_description">
    <div className='about_description_container'>
    <div className='about_description_img'></div>
    <div className='about_description_content' data-aos='fade-left'>
      {description.map((i, index) => {
      let myClass ='';

      if (index % 2 === 0) {
         myClass='description_content_part even'
         
       } else {
         myClass='description_content_part odd'
         
       }
        return <div className={myClass} key={index} >
            <span className="description_content_part_img">
                <FontAwesomeIcon icon={i.icon} />
            </span>
            <p className="description_content_part_text">
            {i.text}
            </p>
        </div>
      })}
        

    </div> 

    </div>
    
    </section>     
 
    );
};

export default AboutDescription;