import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCubes, faKaaba } from '@fortawesome/free-solid-svg-icons';

import styles from './Introduction.scss';
import logo from "../../../assets/images/logo.png";

const Introduction = () => {
    return (
    <section id="introduction">
    <div className="container">
    <article className="introduction_content">
    <div className="app_logo" data-aos="flip-up"></div>
    <p className="description" data-aos="flip-down">
Jestemy firm dziaajc na rynku od maja 1994 roku, co w brany produkcji opakowa daje nam dowiadczenie.

Dowodem tego s klienci zadowoleni ze wsppracy z nami, a take zwikszenie produkcji poprzez zdobywanie nowych rynkw zbytu.
    </p>
    <span className="stamp" data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes} />
    </span>
    
    </article>
    </div>
    </section>      
    );
};

export default Introduction;