import React, { Component } from 'react';
import ScrollToTop from 'react-router-scroll-top'
import {
  BrowserRouter as Router,
  Route,
  NavLink,
  browserHistory,
  Switch
} from 'react-router-dom';
import AOS from 'aos';
import './styles/aos.css'; 


import PageHeader from './components/partials/PageHeader/PageHeader';
import PageFooter from './components/partials/PageFooter/PageFooter';
import Home from './components/pages/HomePage/HomePage';
import About from './components/pages/AboutPage/AboutPage';
import Offer from './components/pages/OfferPage/OfferPage';
import Gallery from './components/pages/GalleryPage/GalleryPage';
import Contact from './components/pages/ContactPage/ContactPage';
import Page404 from './components/pages/Page404/Page404';
import Loader from './components/layouts/Loader/Loader';
import accessData from './accessData.json';

import { hot } from 'react-hot-loader';
import styles from './styles/main.scss';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
        header_class : "header--scrolled",
        loading: true,
        firstLoading: true
      }
      this.timeout = null;

  }
  componentDidMount(){
     this.getDataFromServer();
     AOS.init({duration: 1200, once: true});
}
componentWillReceiveProps (){ 
    AOS.refresh(); 
  } 
 componentWillUnmount = () => {
      clearTimeout(this.timeout);
  };

  scrollHandler = (loading) => {
  if(window.location.pathname != '/'){
      this.setState({header_class: 'header--scrolled'});
    } else{
    if(loading){
this.setState({header_class: 'header--scrolled'});
    } else{
    if (window.scrollY > 40) {
      this.setState({header_class: 'header--scrolled'});
    } else {
      this.setState({header_class: 'header--top'});
    }
    }
    
    }   
  }



isFirstLoading = () => {
let firstLoading = this.state.firstLoading;
     if(this.state.firstLoading){
     this.setState({firstLoading: false});
     this.scrollHandler(false);
     
     } 
      return firstLoading
   
  }
getDataFromServer = () => {
let dataURL = accessData.photosUrl;
    fetch(dataURL, {
    mode: 'cors',
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
       }

    })
      .then(res => res.json())
      .then((res) => {
      let images = [];
      res.forEach((medium, index) => {
      if(medium.mime_type.search("image") == -1) return;
        medium.src = medium.source_url; 
        medium.index = index;     
        images.push(medium);
      });
        let self = this;
        self.timeout = setTimeout(function(){
        self.setState({
          photos: images,
          loading: false,
          firstLoading: true
        });
        }, 500);
      });
}
  render() {
    return (
    <div>
    { this.state.loading ? <Loader loading={this.state.loading}/> : <Router history={browserHistory}>
    
          <div>
          <div id="top"></div>
          <PageHeader headerClass={this.state.header_class}></PageHeader>

            <div className='app_content'>
            <Switch>
            <Route exact path="/" render={props => <Home scrollHandler={this.scrollHandler} isFirstLoading={this.isFirstLoading}/>} />

            <Route path="/o-nas" render={props => <About scrollHandler={this.scrollHandler} isFirstLoading={this.isFirstLoading}/>} />
<Route path="/oferta" render={props => <Offer scrollHandler={this.scrollHandler} isFirstLoading={this.isFirstLoading} {...props}/>} />
<Route path="/galeria" render={props => <Gallery scrollHandler={this.scrollHandler} photos={this.state.photos} isFirstLoading={this.isFirstLoading}/>} />
<Route path="/kontakt" render={props => <Contact scrollHandler={this.scrollHandler} isFirstLoading={this.isFirstLoading}/>} />
<Route render={props => <Page404/> }/>
</Switch>
</div>

            <PageFooter></PageFooter>
          </div>
          
        </Router> }
    
    
        </div>
    );
  }
}

export default hot(module)(App);
