import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCubes, faArrowAltCircleRight } from '@fortawesome/free-solid-svg-icons';
import { BrowserRouter as Router, Route} from "react-router-dom";
import  { HashLink as Link } from "react-router-hash-link";
import icon1 from "../../../assets/images/m1.png";
import icon2 from "../../../assets/images/m2.png";
import icon3 from "../../../assets/images/m3.png";
import icon4 from "../../../assets/images/m4.png";
import icon5 from "../../../assets/images/m5.png";
import icon6 from "../../../assets/images/m6.png";

import styles from './Products.scss';

const productsList = [{image: icon4, title: 'puda klapowe', anchor: '/oferta#offer0'},
{image: icon6, title: 'opakowania wykrojnikowe', anchor: '/oferta#offer1'},
{image: icon3, title: 'opakowania z nadrukiem', anchor: '/oferta#offer2'},
{image: icon2, title: 'przekadki wkady wypenienia', anchor: '/oferta#offer3'},
{image: icon1, title: 'kratownice', anchor: '/oferta#offer4'},
{image: icon5, title: 'przekadki oklejone piank', anchor: '/oferta#offer5'}];

const Products = () => {
    return (
    
    <section id="products">
    <div className='container'>
<span className='stamp' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes} />
</span>
    
    <h3 className='products_header' data-aos='fade-up'>Nasze produkty</h3>
<div className="lines_1" data-aos='fade-up'></div>
    <div className='products_list' data-aos='fade-up'>

{ productsList.map((i, index) => {
        return <div className="product" key={index} data-aos='fade-up' data-aos-delay={index*100} data-aos-duration={700}>
            <Link smooth to={i.anchor}>
                <div className="product_icon_container">
                    <img className="product_icon_image" src={i.image} alt="Logo"/>
                    <div className="product_icon_overlay">
                    <h4>{i.title}</h4>
                    <span className='arrow_go'><FontAwesomeIcon icon={faArrowAltCircleRight} /></span>
                    </div>
                </div>
            </Link>
        </div>

        
      })}

        

        </div> 
        <div className="lines_1" data-aos='fade-up'></div>
        <span className='stamp' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes}/>
</span>

           </div> 
<div>


</div>
    </section>     
 
    );
};

export default Products;