import * as React from "react";


import styles from './Certificates.scss';
import certificate_pl from "../../../assets/images/certificate_pl.jpg";
import certificate_en from "../../../assets/images/certificate_en.jpg";


const Certificates = () => {
    return (
    
    <section id="certificates">
    <h2 className="target" data-aos='fade-up' data-aos-duration='600'> misj firmy jest zadowolenie klienta </h2>
<div className="certificates_container">
    <a target="_blank" href={certificate_pl} data-aos='fade-right' data-aos-duration='600'>
  <img className="certificate" src={certificate_pl} alt="Certyfikat pl"/>
</a>
<a target="_blank" href={certificate_en} data-aos='fade-left' data-aos-duration='600'>
  <img className="certificate" src={certificate_en} alt="Certyfikat en"/>
</a>
</div>
    
    </section>     
 
    );
};

export default Certificates;