import React from 'react';
import style from './HomePage.scss';
import MainSlider from '../../layouts/MainSlider/MainSlider';
import Introduction from '../../layouts/Introduction/Introduction';
import Advantages from '../../layouts/Advantages/Advantages';
import Products from '../../layouts/Products/Products';
import MapContainer from '../../layouts/MapContainer/MapContainer';
import Loader from '../../layouts/Loader/Loader';

class Home extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      loading: true,
      
      }
      this.timeout = null;

   };

  listenScrollEvent = e => {
     this.props.scrollHandler(this.state.loading);
  };

  componentDidMount = () => {
    window.addEventListener('scroll', this.listenScrollEvent);
    window.scrollTo(0, 0);
    if(!this.props.isFirstLoading()){
       this.setPageLoading();
    } else{
       this.setState({loading: false});
    }
    

  };

  setPageLoading = () =>{
      let self = this;
      self.timeout = setTimeout(function(){
      self.setState({loading: false});
      self.props.scrollHandler(self.state.loading);
      }, 500);
  }

  componentWillUnmount = () => {
      clearTimeout(this.timeout);
  };



  render() {
  return (
  <div className="page_container">
  { this.state.loading ? <Loader loading={this.state.loading}/> : 
    <div id='home_page'>
      
      <MainSlider></MainSlider>

      <Introduction></Introduction>
      <Advantages></Advantages>
      <Products></Products>
      <MapContainer></MapContainer>
    </div>}
    </div>
  );
  }
}

export default Home;