import * as React from "react";
import styles from './ContactForm.scss';
import * as emailjs from 'emailjs-com';
import { css } from '@emotion/core';
import { PulseLoader } from 'react-spinners';
import TextField from '@material-ui/core/TextField'
import accessData from '../../../accessData.json';



class ContactForm extends React.Component{
constructor(props) {
      super(props);
    this.state = {
    name: '',
    email: '',
    phone: '',
    subject: '',
    message: '',
    formErrors: {name: '', email: '', phone: '', subject: '', message: ''},
    nameValid: false,
    emailValid: false,
    phoneValid: false,
    subjectValid: false,
    messageValid: false,
    formValid: false,
    buttonClass: 'button sendBtn',
    buttonText: 'Wylij!',
    sendingMessage: false
  };

  
   };

  handleChange = e => {
  const name = e.target.name;
    const value = e.target.value;
        this.setState({[name]: value}, () => { this.validateField(name, value) });

  };

validateField = (fieldName, value) => {
    let fieldValidationErrors = this.state.formErrors;
    let nameValid = this.state.nameValid;
    let emailValid = this.state.emailValid;
    let phoneValid = this.state.phoneValid;
    let subjectValid = this.state.subjectValid;
    let messageValid = this.state.messageValid;

    switch(fieldName) {
    case 'name':
        nameValid = value.length != '';
        fieldValidationErrors.name = nameValid ? '': ' is invalid';
        break;
      case 'email':
        emailValid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
        fieldValidationErrors.email = emailValid ? '' : ' is invalid';
        break;
      case 'phone':
        phoneValid = value.match(/(?<!\w)(\(?(\+|00)?48\)?)?[ -]?\d{3}[ -]?\d{3}[ -]?\d{3}(?!\w)/g);
        fieldValidationErrors.phone = phoneValid ? '': ' is invalid';
        break;
      case 'subject':
        subjectValid = value.length != '';
        fieldValidationErrors.subject = subjectValid ? '': ' is invalid';
        break;
      case 'message':
        messageValid = value.length != '';
        fieldValidationErrors.message = messageValid ? '': ' is invalid';
        break; 
      default:
        break;
    }
    this.setState({formErrors: fieldValidationErrors,
                    nameValid: nameValid,
                    emailValid: emailValid,
                    phoneValid: phoneValid,
                    subjectValid: subjectValid,
                    messageValid: messageValid
                  }, this.validateForm);


}

validateForm() {
    this.setState({formValid: this.state.nameValid && this.state.emailValid && this.state.phoneValid && this.state.subjectValid && this.state.messageValid});
  }

  errorClass(error) {
    return(error.length === 0 ? false : true);
  }

resetForm(self){
setTimeout(function(){
self.setState({ name: '', email: '', phone: '', subject: '', message: '', buttonText: 'Wylij!',
  nameValid: false,
    emailValid: false,
    phoneValid: false,
    subjectValid: false,
    messageValid: false,
    formValid: false,
    buttonClass: 'button sendBtn'
     });
}, 5000);
  
}

sendMessage(templateParams){

     let self=this;
     emailjs.init(accessData.emailjs.user_id);
     emailjs.send("gmail", accessData.emailjs.template_id, templateParams)
        .then(function(response) {
           console.log('SUCCESS!', response.status, response.text);
           self.setState({buttonText: 'Wiadomo wysana!', buttonClass: 'button sendBtn afterSend', sendingMessage: false });
           self.resetForm(self);

        }, function(error) {
           console.log('FAILED...', error);
           self.setState({buttonText: 'Nie udao si wysa wiadomoci. Sprbuj ponownie pniej.', buttonClass: 'button sendBtn afterSend', sendingMessage: false });
           self.resetForm(self);
        });
  
}

onSubmit = () => {
var templateParams = {
        subject: this.state.subject,
        from_name: this.state.name,
        from_email: this.state.email,
        message_html: this.state.message,
        phone: this.state.phone
    };
this.setState({formValid: false, buttonClass: 'button sendBtn clicked', buttonText: 'Wysyanie wiadomoci', sendingMessage: true});
    this.sendMessage(templateParams);
    
}

render(){
return (
<form onSubmit={this.onSubmit} id='contact_form'>  
      <div>    
                 <h5 className="form_title">Masz pytania? <br/> Wylij do nas wiadomo!</h5> 
    </div>
    <div className='form_fields'>
        <TextField name="name"
              label="Imi i nazwisko"
              className="form_input" 
              placeholder="Imi i nazwisko" 
              onChange={this.handleChange}
              value={this.state.name}
              error={this.errorClass(this.state.formErrors.name)}
        />
        <TextField name="email"
              label="E-mail"
              type="email"  
              className="form_input" 
              placeholder="E-mail" 
              onChange={this.handleChange}
              value={this.state.email}     
              error={this.errorClass(this.state.formErrors.email)}
        />
        <TextField name="phone"
              label="Numer telefonu"
              type="tel"  
              className="form_input" 
              placeholder="Numer telefonu" 
              onChange={this.handleChange}
              value={this.state.phone}
              error={this.errorClass(this.state.formErrors.phone)}
        />
        <TextField name="subject"
              label="Temat"
              type="text" 
              className="form_input"  
              placeholder="Temat" 
              onChange={this.handleChange}
    value={this.state.subject}
              error={this.errorClass(this.state.formErrors.subject)}
        />
        <TextField name="message"
              label="Wiadomo"
              type="text" 
              className="form_input"  
              placeholder="Wiadomo" 
              multiline
          rowsMax="3"
          onChange={this.handleChange}
          value={this.state.message}
              error={this.errorClass(this.state.formErrors.message)}
        />
 </div>
        <button type="button" onClick={this.onSubmit} className={this.state.buttonClass} disabled={!this.state.formValid}>
          {this.state.buttonText} 
          <PulseLoader
          css={css`
    display: block;
    margin: 0 auto;
    border-color: #190223;
`}
          sizeUnit={"px"}
          size={10}
          color={'#190223'}
          loading={this.state.sendingMessage}
        />
        </button>
      </form>
);
}
}
export default ContactForm;