import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { Link, NavLink} from 'react-router-dom';
import styles from './PageHeader.scss';
import src from "../../../assets/images/logo.png";
import { Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

class PageHeader extends React.Component {

  constructor(props) {
      super(props);
      this.state = {
      headerExpanded : '',
      hamburgerClass : ''
      }
    
      
   };
 handleMenuClick() {
 if(this.state.menuOpen){
 this.setState({menuOpen: false, headerExpanded: '', hamburgerClass: ''});
 } else{
 this.setState({menuOpen: true, headerExpanded: ' header--expanded', hamburgerClass: 'hamburger--opened'});
 }
    
    }
    handleLinkClick() {
 this.setState({menuOpen: false, headerExpanded: '', hamburgerClass: ''}); 
    
    }
   render() {

  return (
    <header id='page_header' className={this.props.headerClass+' aos-animate ' + this.state.headerExpanded} data-aos="fade-down" data-aos-once='true'>    
    
    <nav className='navbar' aria-expanded='false'>
    <div className='header_container'>
    <div className="navbar__contact navbar__contact--mobile" data-aos="fade-down" data-aos-delay="800" data-aos-once='true'>
              <a href="tel:+48600885980"><span><FontAwesomeIcon icon={faPhone} /></span>+48 600 885 980</a>
              <a href="mailto:peszko@cil.pl"><span><FontAwesomeIcon icon={faEnvelope} /></span>peszko@cil.pl</a>
            </div>
      <div className="navbar__content">
          <div className="navbar__logo site-logo">
            <Link to='/#top'>
              <img src={src} alt="APP Packaging"/>
            </Link>
          </div>

          

          <div className="navbar__links site-nav">
            <div className="navbar__contact" data-aos="fade-down" data-aos-delay="800" data-aos-once='true'>
              <a href="tel:+48600885980"><span><FontAwesomeIcon icon={faPhone} /></span>+48 600 885 980</a>
              <a href="mailto:peszko@cil.pl"><span><FontAwesomeIcon icon={faEnvelope} /></span>peszko@cil.pl</a>
            </div>
            <ul className="site-nav__list">
              <li className="site-nav__item" data-aos="fade-down" data-aos-delay="200" data-aos-once='true'>
                <NavLink exact to="/" activeClassName="current" className="site-nav__link" 
                onClick = {()=>this.handleLinkClick()}>Strona gwna</NavLink>
              </li>
              <li className="site-nav__item" data-aos="fade-down" data-aos-delay="300" data-aos-once='true'>
                <NavLink exact to="/o-nas" activeClassName="current" className="site-nav__link" 
                onClick = {()=>this.handleLinkClick()}>O nas</NavLink>
              </li>
              <li className="site-nav__item" data-aos="fade-down" data-aos-delay="400" data-aos-once='true'>
                <NavLink exact to="/oferta" activeClassName="current" className="site-nav__link"
                onClick = {()=>this.handleLinkClick()}>Oferta</NavLink>
              </li>
              <li className="site-nav__item" data-aos="fade-down" data-aos-delay="500" data-aos-once='true'>
                <NavLink exact to="/galeria" activeClassName="current" className="site-nav__link"
                onClick = {()=>this.handleLinkClick()}>Galeria</NavLink>
              </li>
              <li className="site-nav__item" data-aos="fade-down" data-aos-delay="600" data-aos-once='true'>
                <NavLink exact to="/kontakt" activeClassName="current" className="site-nav__link"
                onClick = {()=>this.handleLinkClick()}>Kontakt</NavLink>
              </li>
            </ul>
          </div>
          <button type="button" role="button" aria-label="Przecz menu" className={'navbar__hamburger '+ this.state.hamburgerClass} onClick = {()=>this.handleMenuClick()} id="menuToggleBtn">
            <span></span>
            <span></span>
            <span></span>
          </button>
        
      </div>
      </div>
    </nav>
    
  </header>
  );
  }
};
export default PageHeader;


