import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCube, faCubes} from '@fortawesome/free-solid-svg-icons';
import style from './OfferPage.scss';
import OffersIntro from '../../layouts/OffersIntro/OffersIntro';
import OffersList from '../../layouts/OffersList/OffersList';
import Loader from '../../layouts/Loader/Loader';


class Offer extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      loading: true,
      
      }
      this.offerIndex = -1;
      this.timeout = null;
      
   };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    this.offerIndex = -1;

    if(!this.props.isFirstLoading()){
       this.setPageLoading();
    } else{
       this.setState({loading: false});
    }
if(this.props.location.state){
  this.setState({anchor: this.props.location.state.anchor});

  }      
      
  };
  componentWillUnmout = () => {
     clearTimeout(this.timeout);
  }


  setPageLoading = () =>{
      let self = this;
      self.timeout = setTimeout(function(){
      self.setState({loading: false});
      self.props.scrollHandler(self.state.loading);
      }, 500);
  }

   render() {
  return (
  <div className="page_container">
  { this.state.loading ? <Loader loading={this.state.loading}/> : 
  <div id='offer_page'>
    <div className="container">
      <h1 className="page_title" data-aos='fade-left'>Nasza oferta</h1>
      <OffersIntro></OffersIntro>
      <OffersList anchor={this.state.anchor}></OffersList>
      <div className="button_container" data-aos='fade-up'>
              <div className="button">
                <Link to="/kontakt" className="site-nav__link">Skontaktuj si z nami!</Link>
              </div>
            </div>
            <span className='stamp' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes} />
</span>
    </div>
    </div>}
    </div>
  );
  }
}

export default Offer;