import React from 'react';
import style from './AboutPage.scss';
import AboutDescription from '../../layouts/AboutDescription/AboutDescription';
import Certificates from '../../layouts/Certificates/Certificates';
import Loader from '../../layouts/Loader/Loader';

class About extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      loading: true,
      
      }
      this.timeout = null;

   };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    if(!this.props.isFirstLoading()){
       this.setPageLoading();
    } else{
       this.setState({loading: false});
    }
    

  };

  setPageLoading = () =>{
      let self = this;
      self.timeout = setTimeout(function(){
      self.setState({loading: false});
      self.props.scrollHandler(self.state.loading);
      }, 500);
  }

  componentWillUnmount = () => {
      clearTimeout(this.timeout);
  };
  
   render() {
  return (
  <div className="page_container">
  { this.state.loading ? <Loader loading={this.state.loading}/> : 
  <div id='about_page'>
    <div className="container">
      <h1 className="page_title" data-aos='fade-left'>O nas</h1>
      <AboutDescription></AboutDescription>
      <Certificates></Certificates>
    </div> 
    </div>}
    </div>
  );
  }
}

export default About;