import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCube, faCubes} from '@fortawesome/free-solid-svg-icons';
import styles from './OffersIntro.scss';

const intro = [{text: "Zajmujemy si produkcj i sprzeda opakowa tekturowych."},
{text: "Jestemy w stanie zrealizowa zarwno mae, jak i wielotysiczne zamwienia."},
{text: "Z przyjemnoci doradzimy Pastwu w zakresie wyboru optymalnego rodzaju opakowania."}];

const OffersIntro = () => {
    return (
    
    <section id="offers_intro"  data-aos='fade-left'>
    
    <div className="intro_description">
      {intro.map((i, index) => {
        return <h4 key={index} data-aos='fade-left' ><span className="offer_intro_icon">
                <FontAwesomeIcon icon={faCube} />
            </span> {i.text}</h4>
        
      })}
        
        </div> 
        <span className='stamp' data-aos='fade-up'>
<FontAwesomeIcon icon={faCubes} />
</span>
    </section>     
 
    );
};

export default OffersIntro;