import React from 'react';
import style from './GalleryPage.scss';
import GalleryList from '../../layouts/GalleryList/GalleryList';
import Loader from '../../layouts/Loader/Loader';

class Gallery extends React.Component {
constructor(props) {
      super(props);
      this.state = {
      loading: true,
      
      }
      this.timeout = null;

   };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    if(!this.props.isFirstLoading()){
       this.setPageLoading();
    } else{
       this.setState({loading: false});
    }
    

  };

  setPageLoading = () =>{
      let self = this;
      self.timeout = setTimeout(function(){
      self.setState({loading: false});
      self.props.scrollHandler(self.state.loading);
      }, 500);
  }

  componentWillUnmount = () => {
      clearTimeout(this.timeout);
  };

   render() {
  return (
  <div className="page_container">
  { this.state.loading ? <Loader loading={this.state.loading}/> : 
  <div id="gallery_page">
    <div className="container">
      <h1 className="page_title" data-aos='fade-left'>Galeria</h1>
      <GalleryList photos={this.props.photos}></GalleryList>
    </div>
    </div>}
    </div>
  );
  }
}

export default Gallery;