import * as React from "react";
import styles from './GalleryList.scss';
import lightbox2 from 'lightbox2/dist/js/lightbox';
import lightboxplusjquery from 'lightbox2/dist/js/lightbox-plus-jquery';
import 'lightbox2/dist/css/lightbox.css'; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearchPlus} from '@fortawesome/free-solid-svg-icons';
import $ from 'jquery';

class GalleryList extends React.Component {

  constructor(props) {
      super();
    this.state = {
      photos: props.photos
    };    
  
   };

  componentWillUnmount(){
  $('#lightbox').hide();
    $('#lightboxOverlay').hide();
  }

   render() {
   lightbox2.option({
      'albumLabel': 'Zdjcie %1 z %2',
      'positionFromTop': 100

    });
  return (
    <section id="gallery_list">
    <div className="photos">
        

{this.state.photos.map((photo, index) => {
      return <a className="photo_link" href={photo.source_url} key={index} data-lightbox="example-set" data-title={photo.title.rendered} data-aos="fade-up">
      <div className='photo_container'>
        <img className="photo" src={photo.source_url} alt=""/>
        <div className="overlay">
    <div className="text button">Zobacz<FontAwesomeIcon icon={faSearchPlus} /></div>
  </div>
      </div>
      </a>
        
      })}
      </div>
      
    </section>
  );
  }
};
export default GalleryList;


