import * as React from "react";
import { Link } from 'react-router-dom';

import Slider from "react-slick";
import styles from './MainSlider.scss';

const MainSlider = () => {
const settings = {
  dots: true,
  arrows: true,
  infinite: true,
      speed: 800,
      autoplay: true,
      autoplaySpeed: 8000,
      slidesToShow: 1,
      slidesToScroll: 1
};
    return (
    <section id="main_slider">
    <div className="slider_container">
    <Slider {...settings} data-aos="fade-up">
          <div>
            <div className="slider_photo1"></div>
            
          </div>
          <div>
            <div className="slider_photo2"></div>
          </div>
          <div>
            <div className="slider_photo3"></div>
          </div>
          <div>
            <div className="slider_photo4"></div>
          </div>
        </Slider>
        <div className="title_container" data-aos="fade-up">
            <h2 className="header_title">PRODUKCJA OPAKOWA TEKTUROWYCH</h2>
            </div>
            <div className="button_container" data-aos="fade-up">
              <div className="button">
                <Link to="/kontakt" className="site-nav__link">Skontaktuj si z nami!</Link>
              </div>
            </div>
    </div>
        
    </section>      
    );
};

export default MainSlider;