import * as React from "react";
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCubes } from '@fortawesome/free-solid-svg-icons';

import styles from './MapContainer.scss';

export class MapContainer extends React.Component {

  render() {
    return (
    <section id='map_container'>
     <h3 className='map_header' data-aos='fade-up'>Znajd nas na mapie</h3>
      <Map google={this.props.google}
    initialCenter={{
            lat: 52.855395,
            lng: 15.841769
          }}
    className={'map'}
    zoom={15} data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
  <Marker
    title={'APP Packaging Peszko A.'}
    name={'SOMA'}
    position={{lat: 52.855395, lng: 15.841769}} />
  
</Map>
 <div className='container'>
<span className='stamp' data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="300"
     data-aos-offset="0">
<FontAwesomeIcon icon={faCubes} />
</span>
</div>
</section>
    );
  }
}
 
export default GoogleApiWrapper({
  apiKey: ('AIzaSyBlOYgiLBmqefORU_T-fIKR6WteD9MzJ4Y')
})(MapContainer)
